@use "variables" as *;

*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

body {
    line-height: 1.5;
}

html {
    box-sizing: border-box;
    font-size: 62.5%; // 10px/16px = 62.5% -> 1rem = 10px
}

ul {
    list-style-type: none;
}

a {
    text-decoration: none;
    color: inherit;
    outline: none;
}

::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}

button {
    background: none;
    border: none;
    font-family: inherit;
    font-size: inherit;
    cursor: pointer;
}